import { Graphics } from "@inlet/react-pixi";
import { Graphics as PixiGraphics } from "@pixi/graphics";
import { isNull, isUndefined, keyBy } from "lodash";
import { TraceEvent } from "protocol/Trace";
import { useMemo } from "react";
import { getColor } from "../colors";
import { scale } from "../planar-renderer/config";
import { Square } from "../planar-renderer/Draw";
import { Point } from "../Renderer";
import { Scale } from "../Scale";

const WEIGHT = 3 / scale;

function defined<T>(obj: T): obj is Exclude<T, undefined | null> {
  return !isUndefined(obj) && !isNull(obj);
}

type PathProps = {
  nodes?: TraceEvent<"cx" | "cy">[];
  step?: number;
  scale?: Scale<Point>;
};

export function Path({ nodes = [], step = 0, scale }: PathProps) {
  const path = useMemo(() => {
    const memo = keyBy(nodes, "id");
    return (s: number) => {
      const out = [];
      let next: TraceEvent | undefined = nodes[s];
      while (next) {
        out.push(next);
        next = defined(next.pId) ? memo[`${next.pId}`] : undefined;
      }
      return out;
    };
  }, [nodes]);

  const draw = useMemo(() => {
    const p = path(step);
    return (g: PixiGraphics) => {
      g.clear();
      for (const [i, node] of p.entries()) {
        const { cx, cy } = node?.variables ?? {};
        const { x = 0, y = 0 } = scale?.to?.({ x: cx, y: cy }) ?? {};
        g.lineTo(x, y);
        if (!i) g.lineStyle(WEIGHT, getColor("source"));
      }
    };
  }, [path, step, scale]);

  const { cx = 0, cy = 0 } = nodes[step]?.variables ?? {};

  return (
    <>
      <Graphics draw={draw} />
      {nodes?.[step] && (
        <Square {...scale?.to?.({ x: cx, y: cy })} color={getColor("source")} />
      )}
    </>
  );
}
